    p(data-aos="fade-down") En este reconocimiento como sujeto de derechos, la familia decide con quin accede a los procedimientos contemplados en la RPMS- Rutas de Promocin y Mantenimiento de la Salud para la Familia, pudiendo ser direccionada hacia aquella EAPB- Entidades Administradoras de Planes de Beneficios de Salud que tenga afiliada al mayor nmero de los integrantes de la familia. Siendo en todo caso de obligatorio cumplimiento el que todos los aseguradores oferten y brinden todas las intervenciones individuales establecidas para la familia.

