      .col-lg-6.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/37.png', alt='')


    .row.justify-content-center.align-items-center.mb-3
      .col-lg-6.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/38.png', alt='')               
      .col-lg-6.my-3
        p(data-aos="fade-down") Y por otro, se hace una descripcin de la valoracin del componente familiar: este dice que la valoracin integral a la familia implica conocer la estructura, las relaciones y las dinmicas que se determinan al interior de la familia; las condiciones individuales de cada uno de sus integrantes que afectan a la familia como sistema abierto; las capacidades y los recursos personales y familiares que inciden en la salud familiar de las personas, adems de la red de apoyo con la que cuenta.
        h5(data-aos="fade-down") Todos los anteriores son factores de riesgo o protectores que afectan positiva o negativamente la salud familiar y su capacidad de respuesta y adaptacin.           

    .row.justify-content-center.align-items-center.mb-5           
        .col-lg-10.bg15.p-3.px-5.pb-4
          .row.justify-content-center.align-items-center       
            .col-lg-auto.my-3
              img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/39.svg', alt='')
            .col.my-3
              h4(data-aos="fade-down") Importante
              p(data-aos="fade-down").mb-0 La identificacin del riesgo se apoya en la evaluacin del grado de satisfaccin de la funcionalidad familiar, principalmente mediante la aplicacin del instrumento #[b APGAR familiar - Instrumento de valoracin]. Es recomendable que este instrumento sea utilizado conjuntamente con otros instrumentos sugeridos en este anexo para la valoracin familiar.

    h4(data-aos="fade-down").text-center.mb-5 La valoracin familiar se realiza:

    .row.justify-content-center.mb-3
      .col-lg-6.my-3
        .bg16.p-4.h-100
          ul.lista-ul.mb-0.fa-ul
            li 
              i.lista-ul__vineta.c3
              p.mb-0 Mediante la indagacin con la persona y/o sus acompaantes familiares.

            li 
              i.lista-ul__vineta.c3
              p.mb-0 En el marco de las consultas de valoracin integral en los diferentes momentos del curso de vida

            li 
              i.lista-ul__vineta.c3
              p.mb-0 Con dos o ms integrantes del grupo familiar directamente, en el caso de la atencin bsica de orientacin familiar. Esta atencin debe realizarse de acuerdo con las frecuencias establecidas para la atencin en salud en cada momento del curso de vida.

            li.mb-0 
              i.lista-ul__vineta.c3
              p.mb-0 As mismo, cuando el grupo familiar demande la atencin bsica de orientacin familiar. 
      .col-lg-6.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/40.png', alt='')  

    .row.justify-content-center.align-items-center.mb-3       
      .col-lg-auto.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/41.svg', alt='')
      .col.my-3
        p(data-aos="fade-down") La informacin resultante de la valoracin familiar debe estar disponible de manera permanente y cuando los integrantes de la misma reciban cualquier tipo de atencin en salud, de tal modo que pueda ser actualizada, ampliarse, verificarse o profundizarse. Es de anotar que toda informacin derivada de la valoracin familiar debe manejarse como perteneciente a la #[b historia clnica] y, por tanto, debe cumplir con todos los requisitos de la #[b proteccin de datos].

        p(data-aos="fade-down").mb-0 En la anamnesis de las consultas de valoracin integral de las personas en los diferentes momentos del curso de vida y en la atencin bsica de orientacin familiar se debe indagar por los siguientes aspectos que configuran el componente de la valoracin familiar:


    .row.justify-content-center.align-items-center.mb-3          
      .col-lg-8.my-3(data-aos="fade-left")
        AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta bg17")
          .row.justify-content-center(titulo="Estructura y dinmica")
            .col-12
              p Estructura y dinmica familiar mediante la realizacin del Familiograma y APGAR familiar para identificar la conformacin de la familia y las relaciones entre sus integrantes, a fin de identificar las situaciones que repercutan en la salud y el desarrollo, en el momento del curso vital de la familia y la vivencia de sucesos normativos y no normativos. 
          .row.justify-content-center(titulo="Situaciones particulares en salud")
            .col-12
              p Cada familia puede contar con integrantes que son vulnerables porque presentan cualquier tipo de discapacidad, sufren patologas crnicas, enfermedades hurfanas o terminales, que afectan directamente la estabilidad de los miembros de la familia.  
          .row.justify-content-center(titulo="Expectativas e inquietudes")
            .col-12
              p Expectativas e inquietudes de familia respecto al cuidado de su salud y sobre el acceso a los servicios de salud para sus integrantes. Asimismo, indagar sobre sus visiones frente a condiciones que afectan su salud y la de su familia, en los entornos donde se desarrolla y sobre experiencias previas de atencin.                                   
      .col-lg-4.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/42.png', alt='')

    p(data-aos="fade-down").mb-5 Cuando la valoracin familiar se realice en el procedimiento de atencin bsica de orientacin familiar la indagacin puede contemplar:

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-12(data-aos="fade-left")     
        figure
          .video
            iframe(width="560" height="315" src="https://www.youtube.com/embed/1HNElLUZxNA" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen) 
        figcaption Video. Atencin bsica para la orientacin familiar
    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-8               
        p(data-aos="fade-down").text-center A partir de los hallazgos de la valoracin del componente familiar y principalmente de los resultados de la aplicacin del #[b Apgar familiar] se debe clasificar el riesgo de las  familias as: 

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-6.my-3      
        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img.d-block
            img.img-t.img-a.pt-3(src='@/assets/curso/temas/44.svg' alt='')
          .tarjeta.bg18
            .p-4.ps-0
              h4 Riesgo alto: menor a 9 puntos.
              p.mb-0 Son aquellas en las que se detecta un evento que las est afectando directamente, las cuales deben derivarse a las rutas de riesgo o de evento segn sea el caso, de acuerdo con el anlisis de la problemtica familiar que presenten, sin perjuicio de la canalizacin a los servicios sociales que resulten pertinentes.   

        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img.d-block
            img.img-t.img-a.pt-3(src='@/assets/curso/temas/45.svg' alt='')
          .tarjeta.bg19
            .p-4.ps-0
              h4 Riesgo medio: entre 10 y 12 puntos.
              p.mb-0 Familias  que deben ser derivadas a la consulta de orientacin bsica familiar. 

        .tarjeta-avatar-b
          .tarjeta-avatar-b__img.d-block
            img.img-t.img-a.pt-3(src='@/assets/curso/temas/46.svg' alt='')
          .tarjeta.bg20
            .p-4.ps-0
              h4 Riesgo bajo: entre 13 y 16 puntos.
              p.mb-0 Familias valoradas segn la informacin en salud  y acorde con sus particularidades, si es pertinente sern canalizadas a los servicios sociales.                                
      .col-lg-6.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/43.png', alt='') 

    p(data-aos="fade-down").mb-5 Adicionalmente, segn el criterio del profesional que realiza la valoracin por momento de curso de vida, se pueden considerar otros criterios que permitan definir el riesgo de las familias, por ejemplo, se sugieren los siguientes elementos para la derivacin de la atencin bsica de orientacin familiar:

    .tarjeta.tarjeta-verde.p-4(data-aos="fade-right") 
      SlyderA(tipo="b")
        .row.justify-content-center.mt-3
          .col-lg-6.mb-4.mb-md-0.my-3
            h4 Suceso importante
            p Familias con vivencia de sucesos vitales que no puedan afrontar la situacin y afecten la salud del grupo, porque no han desarrollado resiliencia de manera individual y colectiva. 
        
          .col-lg-5.my-3
            img.img-a.img-t(src='@/assets/curso/temas/48.png', alt='')     
        .row.justify-content-center.mt-3
          .col-lg-6.mb-4.mb-md-0.my-3
            h4 Situacin de vulnerabilidad
            p Familias en situaciones de #[b vulnerabilidad social] que pueden afectar la salud familiar, por ejemplo, familias con varios integrantes en condicin de dependencia y un solo proveedor o expuestas a violencia intrafamiliar, al consumo de SPA, a la explotacin sexual o econmica, al pandillismo o familias dedicadas al reciclaje, etc... 
        
          .col-lg-5.my-3
            img.img-a.img-t(src='@/assets/curso/temas/49.png', alt='')              
        .row.justify-content-center.mt-3
          .col-lg-6.mb-4.mb-md-0.my-3
            h4 Familias especiales
            p Familias con algn integrante en #[b condicin de discapacidad] que puede presentar algn tipo de deficiencia fsica, intelectual, mental o sensorial a largo plazo, que al interactuar con el entorno inmediato encuentra barreras de diferentes tipos para poder contactarse con otros o con el medio. 
        
          .col-lg-5.my-3
            img.img-a.img-t(src='@/assets/curso/temas/50.png', alt='')     
        .row.justify-content-center.mt-3
          .col-lg-6.mb-4.mb-md-0.my-3
            h4 Redes de apoyo
            p Familias #[b con falta] de redes de apoyo emocional o son #[b deficientes], y en algunos casos generan #[b riesgo o afectacin] la salud familiar o de la persona en especial, por su silencio. 
        
          .col-lg-5.my-3
            img.img-a.img-t(src='@/assets/curso/temas/51.png', alt='')  
        .row.justify-content-center.mt-3
          .col-lg-6.mb-4.mb-md-0.my-3
            h4 Salud en condicin crtica
            p Familias con prcticas del cuidado de #[b salud crtica] de varios de sus integrantes por enfermedad o por vejez y que ponen en riesgo o afecta la salud de la familia en general, debido al estrs del cuidado que debe hacerse del grupo. 
        
          .col-lg-5.my-3
            img.img-a.img-t(src='@/assets/curso/temas/52.png', alt='')  

    p(data-aos="fade-down").my-5 Las familias pueden ser derivadas al procedimiento de educacin para la salud grupal, acorde con intereses o necesidades identificados y concertados, pero cmo se hace y cules elementos se deben tener en cuenta, vea primero los objetivos:  

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-6.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/53.png', alt='')       
      .col-lg-6.my-3      
        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img
            img.img-t.img-a(src='@/assets/curso/temas/54.svg' alt='')
          .tarjeta.bg21
            .p-4.ps-0
              p.mb-0 Ampliar la valoracin de las capacidades y condiciones de funcionalidad de la familia, identificando factores protectores y de riesgo para la salud.

        .tarjeta-avatar-b.mb-3
          .tarjeta-avatar-b__img
            img.img-t.img-a(src='@/assets/curso/temas/55.svg' alt='')
          .tarjeta.bg21
            .p-4.ps-0
              p.mb-0 Brindar orientaciones bsicas acordes con la situacin familiar particular, el estado de las relaciones y la salud familiar, que aporten a su dinmica y equilibrio en funcin del logro en salud.

        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img.img-t.img-a(src='@/assets/curso/temas/56.svg' alt='')
          .tarjeta.bg21
            .p-4.ps-0
              p.mb-0 Definir las atenciones requeridas de acuerdo con las particularidades de la situacin y lo concertado con las familias que harn parte del plan integral de cuidado primario.                            

    p(data-aos="fade-down").mb-5 Por otro lado, uno de los factores importantes que se debe tener en cuenta es la poblacin sujeto que se va a trabajar, por lo que todo el equipo interdisciplinario debe, con base en los datos recolectados, caracterizarla segn las siguientes pautas:

    .row.justify-content-center.align-items-start.mb-4           
      .col-lg-5.my-3
        LineaTiempoD.color-primario
          p(numero="1" titulo="Riesgo") Familias con situaciones o condiciones de riesgo medio para la salud, identificadas en las intervenciones/atenciones en salud individuales.

          p(numero="2" titulo="PIC - Plan de intervenciones colectivas") Familias canalizadas desde las intervenciones del PIC. 
          p(numero="3" titulo="Solicitud de orientacin") Familias que demandan espontneamente en conjunto o por alguno de sus integrantes aduciendo alguna necesidad de orientacin familiar.
      .col-lg-5.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/57.png', alt='')  

    p(data-aos="fade-down").mb-5 Para logar este proceso de orientacin familiar se requiere un equipo interdisciplinario conformado por profesionales de diferentes reas del conocimiento, especialmente del rea de la salud y de las ciencias sociales que analizan las caracterizaciones familiares y dan respuestas en cada caso, estos son:

    .bg22
      .row.justify-content-center.align-items-center.mb-3
        .col-lg-4.up.my-3
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/59.png', alt='')                      
        .col-lg-8.up.my-3
          SlyderF.text-center.mb-5(columnas="col-lg-6")(data-aos="fade-right")
            .tarjeta.bgw.ts.h-100.p-4.sha
              .row.justify-content-center.align-items-center.mb-3           
                .col-auto.bgn.p-2
                  h5.mb-0.mx-1(data-aos="fade-down") 1          
              h4 Mdico de familia  
              p.mb-0 Persona encargada de tener el primer contacto desde el rea de la salud con uno de los miembros de la familia y quien conoce el caso de prima mano, por lo que direcciona a psicologa y a otros servicios de salud o servicios externos como comisaras de familia.   
              
            .tarjeta.bgw.ts.h-100.p-4.sha
              .row.justify-content-center.align-items-center.mb-3           
                .col-auto.bgn.p-2
                  h5.mb-0.mx-1(data-aos="fade-down") 2          
              h4 Psiclogo o Psicloga  
              p.mb-0 Encargados de establecer contacto directo con el paciente o el grupo familiar para iniciar el proceso y direccionar el caso segn se requiera, por lo que tambin hace seguimiento hasta lograr su finalizacin.

            .tarjeta.bgw.ts.h-100.p-4.sha
              .row.justify-content-center.align-items-center.mb-3           
                .col-auto.bgn.p-2
                  h5.mb-0.mx-1(data-aos="fade-down") 3          
              h4 Trabajadora social o Trabajador social  
              p.mb-0 Quienes hacen las visitas domiciliarias y son los que verifican las situaciones familiares en el contexto real.

            .tarjeta.bgw.ts.h-100.p-4.sha
              .row.justify-content-center.align-items-center.mb-3           
                .col-auto.bgn.p-2
                  h5.mb-0.mx-1(data-aos="fade-down") 4          
              h4 Enfermara o Enfermero  
              p.mb-0 Son los encargados de hacer seguimiento y llevar el registro del paciente o de la familia segn el caso.
              
            .tarjeta.bgw.ts.h-100.p-4.sha
              .row.justify-content-center.align-items-center.mb-3           
                .col-auto.bgn.p-2
                  h5.mb-0.mx-1(data-aos="fade-down") 5         
              h4 Nutricionista  
              p.mb-0 Para hacer seguimiento en temas de alimentacin y bienestar.

            .tarjeta.bgw.ts.h-100.p-4.sha
              .row.justify-content-center.align-items-center.mb-3           
                .col-auto.bgn.p-2
                  h5.mb-0.mx-1(data-aos="fade-down") 6          
              h4 Abogado o Abogada  
              p.mb-0  Para revisar la vulneracin de derechos.

    p(data-aos="fade-down").pt-5.mb-4 Por otro lado, se hace necesario revisar el tipo de atenciones incluidas en este tipo de servicios de salud para las familias, entre los cuales se encuentra: 

    .row.justify-content-center.mb-3           
      .col-lg-2.p-0.my-3
        img.img-a.img-mov(data-aos="zoom-in")(src='@/assets/curso/temas/60.svg', alt='') 
      .col-lg-8.p-0.my-3
        .bg23.p-3.h-100.j(data-aos="flip-down")
          ul.lista-ul.mb-0.fa-ul
            li 
              i.lista-ul__vineta.c4
              p.mb-0 Valorar la estructura y la dinmica familiar como apoyo al desarrollo integral de cada miembro.
            li 
              i.lista-ul__vineta.c4
              p.mb-0 Valorar el contexto social familiar.  
            li 
              i.lista-ul__vineta.c4
              p.mb-0 Revisar las redes de apoyo sociales y comunitarias con las que cuenta la persona y la familia.  
              
    .row.justify-content-center.align-items-start.mb-3          
      .col-lg-4.my-3(data-aos="flip-down")
        h5(data-aos="fade-down") Por ltimo, con el mismo nivel de importancia, es necesario revisar la descripcin del procedimiento que se presenta a continuacin:
        p(data-aos="fade-down").mb-5 Esta consulta de atencin bsica de orientacin familiar implica un abordaje profesional que tiene como premisa el dilogo y la escucha activa con la familia a fin de brindar orientacin, asesora integral y apoyo en salud, centrado en aspectos de la situacin y la dinmica familiar que permiten a los participantes de la atencin identificar las posibilidades de fortalecimiento, la resolucin de conflictos, la identificacin de recursos, las estrategias de afrontamiento y las oportunidades de actuacin que redunden en beneficio de su salud familiar.
        img.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/61.svg', alt='')                      

      .col-lg-3.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/62.png', alt='')                      

      .col-lg-5.my-3
        .bg24.p-3.brad.mb-4(data-aos="flip-down")  
          p(data-aos="fade-down").mb-0 Es prescindible que asista preferiblemente el #[b ncleo principal de la familia], como mnimo con dos integrantes, de preferencia contar con la presencia de un adulto, bien sea cuando la familia accede por demanda espontnea o cuando es canalizada o es derivada de una atencin individual de una #[b RIAS - Rutas Integrales de Atencin en Salud] u otro tipo de atencin.
        .bg25.p-3.brad(data-aos="flip-down")
          .row.justify-content-center.align-items-center          
            .col.order-1.my-3     
              h4(data-aos="fade-down") Atencin  
              p(data-aos="fade-down").mb-0 Adems, si es necesario evaluar otros aspectos o profundizar en el abordaje se buscar la participacin de otros integrantes. Esta consulta de atencin bsica de orientacin familiar comprende varias reas de la salud y el bienestar del ser humano.   
            .col-lg-auto.order-lg-2.my-3
              img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/63.svg', alt='') 

    Separador 
    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 2.2 E - Enfermedad                                  
    h4(data-aos="fade-down").mb-3 Derivacin a una RIAS de riesgo o atencin resolutiva (nfasis en el manejo de los hallazgos y la articulacin de los mismos en el plan de cuidado). 

    .row.justify-content-center.align-items-start.mb-3           
      .col-lg-3.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/64.png', alt='')
      .col-lg-9.my-3
        p(data-aos="fade-down") Son acciones que garantizan el cuidado de la salud del individuo teniendo en cuenta la deteccin temprana, la educacin y la proteccin especfica en salud en una comunidad, potencializando las actividades fsicas, cognitivas y psicolgicas para alcanzar las metas en su desarrollo intelectual, en esta fase se #[b detecta oportuna y tempranamente el riesgo en salud que incluye reducir el dao que emerge en el momento vital de la vida y evitar afectar el entorno, buscando que sea protector y aumentar el desarrollo para la salud.]

        p(data-aos="fade-down") Este grupo de riesgo es definido como el conjunto de personas con condiciones comunes de exposicin y vulnerabilidad a ciertas patologas que se presentan, estos factores de riesgo derivan desenlaces clnicos y las estrategias eficaces de la entrega de los servicios en la atencin.
        .row.justify-content-center.align-items-center         
          .col-lg-auto.my-3                  
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/65.svg', alt='')
          .col.my-3
            p(data-aos="fade-down").mb-0 #[b.t1 La clasificacin de los grupos de riesgo brinda, con claridad, las herramientas a los tomadores de decisin locales,] cumpliendo con principios de eficiencia y coherencia entre los distintos programas de salud pblica. Para los grupos de riesgo conformados se define un proceso de atencin integral que incluye las acciones intersectoriales y sectoriales de gestin de la salud pblica, e intervenciones individuales y colectivas, asumiendo que los #[b.t1 grupos de riesgo conservan caractersticas iguales,] establecida como proceso de atencin integral que permite su gestin integral.                  

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-6
        .bgy.p-4.text-center(data-aos="flip-down")  
          p(data-aos="fade-down").mb-0 Existen 16 grupos de riesgo definidos por el #[b MSPS- Ministerio  de Salud y Proteccin Social, a saber:]    
    .row.justify-content-center.align-items-start.mb-3.text-center           
      .col-lg-2.col-md-4.my-3
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/66.svg', alt='')
        p(data-aos="fade-down").mb-0 Materno- perinatal
      .col-lg-2.col-md-4.my-3
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/67.svg', alt='')
        p(data-aos="fade-down").mb-0 Alteraciones nutricionales
      .col-lg-2.col-md-4.my-3
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/68.svg', alt='')
        p(data-aos="fade-down").mb-0 Enfermedades infecciosas
      .col-lg-2.col-md-4.my-3
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/69.svg', alt='')
        p(data-aos="fade-down").mb-0 Trastornos asociados al uso de spa 
      .col-lg-2.col-md-4.my-3
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/70.svg', alt='')
