    .row.justify-content-center.align-items-center.mb-5    
      .col-lg-10.mt-5
        .bg26.p-4.brad(data-aos="flip-down")  
          p(data-aos="fade-down").mb-0 Estos grupos de riesgo son priorizados por las entidades territoriales de acuerdo con el anlisis de la situacin en salud, para llevar a cabo acciones de promocin de la salud e intervenciones sectoriales e intersectoriales, as como realizar las interfaces con los aseguradores para orientar las intervenciones individuales y colectivas que conlleven a los resultados esperados en salud en su gobierno.

    Separador 
    #t_2_3.titulo-segundo.color-acento-contenido.mb-3(data-aos="zoom-in-left")
      h2 2.3 R - Ruta de PMS

    .row.justify-content-center.align-items-center.mb-3    
      .col-lg-8.my-3
        p(data-aos="fade-down") Se entiende la #[b Ruta de PMS- Ruta de Promocin y Mantenimiento de la Salud], la cual fue regulada por la Resolucin 3202 de 2016, como el conjunto de acciones coordinadas, complementarias y efectivas para garantizar el derecho a la salud, expresadas en polticas, planes, programas, proyectos, estrategias y servicios, que se materializan en atenciones dirigidas a las personas, las familias y las comunidades para la promocin de la salud, la prevencin de la enfermedad, el diagnstico, el tratamiento, la rehabilitacin y los cuidados paliativos en los diferentes estadios de la vida segn las #[b RIA], para esto se deben tener en cuenta cada una de las etapas etarias atendidas, sus caractersticas y las condiciones propias de cada ser humano.
      .col-lg-4.my-3 
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/81.svg', alt='')
    h4(data-aos="fade-down").mb-5 Por tanto, se invita a revisar el siguiente video que habla al respecto:

