      )
      .bloque-texto-g__texto.p-5
        p.mb-0 El objeto del Decreto 3518 es crear y reglamentar el #[b Sistema de Vigilancia en Salud Pblica, SIVIGILA], para la provisin en forma sistemtica y oportuna de informacin sobre la dinmica de los eventos que afecten o puedan afectar la salud de la poblacin, con el fin de orientar las polticas y la planificacin en salud pblica. 
        
    p(data-aos="fade-down") Tomar las decisiones para la prevencin y el control de las enfermedades y los factores de riesgo en salud; optimizar el seguimiento y la evaluacin de las intervenciones; racionalizar y optimizar los recursos disponibles y lograr la efectividad de las acciones en esta materia, propendiendo por la proteccin de la salud individual y colectiva.


    h4(data-aos="fade-down").my-5 A continuacin, se mencionan algunas definiciones que conforman dicho sistema y que son importantes al momento de disear un plan de accin para una poblacin en temas de salud:

    .row.justify-content-center.align-items-center          
