        p(data-aos="fade-down") La clave en relacin con los hallazgos del instrumento y el alcance del mismo son las condiciones de vida, las concepciones y las prcticas que promueven y mantienen la recuperacin de la salud y favorecen el desarrollo integral del individuo, la familia, la comunidad, dirigidas a las intervenciones de las atenciones en salud integral que tiene cada individuo, adems analiza los resultados de las intervenciones en salud sanitarias y las acciones que del sector inciden en los determinantes sociales en salud.
