    .row.justify-content-center.align-items-center.mb-3    
      .col-lg-8.my-3
        p(data-aos="fade-down") Esta accin se encamina a los conjuntos de poblaciones que tienen situaciones de riesgo o que inciden en la salud y el bienestar de una comunidad.
        p(data-aos="fade-down") Esta intervencin colectiva #[b.t1 PIC- Plan de Intervenciones Colectivas]  complementa el #[b.t1 Plan Obligatorio en Salud]  y genera otros beneficios, que impactan positivamente todos los determinantes sociales en salud, a travs de planes de ejecucin de intervenciones individuales y colectivas en salud y el desarrollo de actividades a lo largo de la vida para el beneficio de todo integrante de una poblacin.   
        p(data-aos="fade-down") Partiendo de lo anterior, se tratan las intervenciones de educacin para la salud como intervenciones de informacin en salud que se complementen y aporten al logro de los objetivos que el territorio se propuso en el marco de su planeacin territorial.     
