        p(data-aos="fade-down").mb-0 Por otra parte, en la Resolucin 518 de 2015 se estableci dentro de los procesos de gestin de la salud pblica el desarrollo de capacidades, que pretenden generar transformaciones y condiciones para crear, construir o fortalecer los conocimientos, las habilidades y las actitudes que se requieren para gestionar tcnica, administrativa y financieramente las polticas de salud y proteccin social, de forma que aporten a mejorar la salud de los habitantes de los distintos territorios del pas.         

    p(data-aos="fade-down").mb-5 Los contenidos de cada RIAS se registran a travs de dos formas: la matriz de acciones/intervenciones y los diagramas de accin/ intervencin. Matriz de acciones / intervenciones: #[b esta matriz est conformada por cinco componentes correspondientes al paso 7 del Manual metodolgico, conzcalos:]

