        :style="{'background-image': `url(${require('@/assets/curso/temas/95.png')})`}"
      )
      .bloque-texto-g__texto.p-5
        p.mb-0 La educacin en salud ha tomado mucha importancia en el Siglo XXI, debido a que la industrializacin de los alimentos ha hecho que estos sean cultivados, manufacturados y entregados al cliente final con muchos elementos adicionales que le producen enfermedades a largo plazo, por lo que las entidades multilaterales y los gobiernos han tenido que disear polticas fuertes, con el fin de definir los parmetros que lleven al consumidor a crear conciencia sobre lo que compran y llevan a la mesa, y a las empresas a etiquetar los productos o a pagar impuestos o multas. 

    p(data-aos="fade-down").mb-5 Por esta razn, la educacin en salud debe cubrirse de diferentes formas y a diferentes niveles para llegar a toda la poblacin a largo plazo, segn los protocolos del Ministerio de Salud y Proteccin Social se debe hacer a travs de sesiones de la siguiente forma:   
