              h4(data-aos="fade-down").mb-0 Para qu? (nfasis)   

          p(data-aos="fade-down").mb-5 Dentro del proceso de formacin en salud se hace necesario tener claro cul es el objetivo que se tiene al citar al individuo, el grupo, el colectivo o la comunidad, ya que solo hay cambios reales si se tocan las fibras ms cercanas a las necesidades del ser, por lo que es de suma importancia definir el para qu con claridad y se hace mediante los siguientes parmetros. 
