                    p.mb-0 Aprender e implementar prcticas protectoras de vida (cuidado de la salud propia y de la familia), motivando la toma de decisiones que sean necesarias para adoptar estilos de vida saludables y lograr ms control sobre su vida.

                ul.lista-ul.mb-0.fa-ul
                  li 
                    i.lista-ul__vineta.c4
                    p.mb-0 Desarrollar autoconocimiento y autoestima, que sirva de base para construir prcticas de autocuidado.

                ul.lista-ul.mb-0.fa-ul
                  li 
                    i.lista-ul__vineta.c4
                    p.mb-0 Para promover capacidades de cuidado y para fortalecer las relaciones y los vnculos familiares que inciden en la salud familiar.

