          p(data-aos="fade-down").mb-5 Otro elemento importante a tener en cuenta es definir cundo se va a implementar cada una de las sesiones con el individuo o el grupo de personas segn el objetivo propuesto, ya que se debe definir la intensidad de las mismas a corto, mediano o largo plazo para poder ver los cambios reales, estas pueden ser:    

