              h4(data-aos="fade-down").mb-0 Cmo? (herramientas de aprendizaje) 

          p(data-aos="fade-down") Adicionalmente un profesional en salud debe tener presente las estrategias que va a utilizar para llegar a la conciencia de las personas para lograr el cambio de hbitos o la resolucin de problemas, por lo que se hace necesario utilizar diferentes tipos de herramientas de aprendizaje acordes con la edad, con los ritmos de aprendizaje y con las condiciones del grupo al que va dirigida la sesin de formacin en salud.

          h5(data-aos="fade-down").mb-4 Por esa razn, es vital hacer una revisin al tema, por lo que dependiendo de la poblacin se define el cmo se va a abordar la intervencin, que puede ser:             

          .bg-t.ps-5.p-3.mb-4
            h5(data-aos="fade-down").mb-0 Individuales
          .px-3
            p(data-aos="fade-down") Estas se realizan teniendo en cuenta las condiciones de salud de la persona y segn la informacin remitida por el profesional de salud, a travs de:

            ul.lista-ul.mb-0.fa-ul                    
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Tutora con un plan de actividades a realizar.
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Plan de acompaamiento presencial o semipresencial (con herramientas virtuales: correo, chat, pginas interactivas, telemedicina, teleorientacin, etc.).
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Tcnicas de consejera, por lo que se establece un vnculo emptico entre el consejero y el aconsejado, que permite trabajar sobre los temas definidos en la consulta y se construyen posibles soluciones entre los dos.             

          .bg-t.ps-5.p-3.my-4
            h5(data-aos="fade-down").mb-0 Grupales
          .px-3
            p(data-aos="fade-down") Estas se realizan a partir de la caracterizacin poblacional, que se obtiene de la recoleccin de informacin, su sistematizacin, anlisis para la toma de decisiones a nivel de salud pblica, que logre impactar un sector importante de la sociedad para prestar mejores servicios y fortalecer los derechos de los colombianos o los extranjeros que vivan en el pas. Para esto se pueden usar las siguientes herramientas:

            ul.lista-ul.mb-0.fa-ul                    
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Proyectos participativos.
              li 
                i.lista-ul__vineta.c4
