                h4(data-aos="fade-down") Importante
                p(data-aos="fade-down").mb-0 En general, se prefieren las actividades que impliquen movimiento y no estar sentados en un saln (pueden incluir momentos especficos o transversalizar las acciones con una propuesta recreativa o deportiva).     

          p(data-aos="fade-down").mb-5 Lo anterior, puede implementarse en el proceso de la #[b.t1 EPS-Entidad Promotora en Salud] (en su propia intervencin: de educacin comunitaria y comunicacin para la salud) o a travs de las intervenciones del #[b.t1 PIC- Plan de Intervenciones Colectivas:]   

