            .col-auto.bg3.p-2(data-aos="flip-down")
              h4(data-aos="fade-down").mb-0 Quin organiza el proceso educativo?

          p(data-aos="fade-down") El diseo pedaggico y metodolgico estar a cargo de las #[b.t1 EAPB- Entidades Administradoras de Planes de Beneficios] en el caso de las sesiones individuales y grupales, y de las #[b.t1 Direcciones Territoriales de Salud en el caso de las sesiones] colectivas. Esto deber realizarse con la asesora de un profesional de Psicologa, Pedagoga o Psicopedagoga.

          p(data-aos="fade-down") Para el caso de las comunidades tnicas se tendr en cuenta a su institucionalidad en salud #[b.t1 (sabios, sabedores, etc.),] estos pueden ser:
      .row.justify-content-center.align-items-start.mb-4 
        .col-lg-5.my-3
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/103.png', alt='')                   
        .col-lg-5.my-3
          LineaTiempoD.color-primario
            p(numero="1" titulo="Individuales") Profesional de la salud o especialista segn cada caso particular. En este caso se requiere hacer trabajo integrado, independiente y paralelo a corto,  mediano o largo plazo para ver los resultados.
            p(numero="2" titulo="Grupales") Profesionales y tcnicos (de acuerdo con las capacidades a desarrollar y los temas a tratar). La planeacin del desarrollo de las sesiones grupales se har con la participacin del equipo multidisciplinario.
            p(numero="3" titulo="Colectivas") Equipo(s) multidisciplinario(s) contratado(s) para implementar la(s) accin(es) o proyectos PIC, que incluya profesionales y tcnicos en salud y de salud ambiental, Ciencias Sociales u otras reas del conocimiento que se requieran para el desarrollo de capacidades en este nivel; en conjunto con los sujetos, las familias o las comunidades.
                 
        .col-lg-10.mt-5
          .row.justify-content-start.mb-4          
            .col-auto.bg3.p-2(data-aos="flip-down")
              h4(data-aos="fade-down").mb-0 Con quin(es)?

          p(data-aos="fade-down") Otro factor a tener en cuenta al momento de disear una sesin es pensar en el sujeto de la atencin (individuo o familia del afiliado). #[b Eventualmente si se trata de un menor de edad o una persona con discapacidad o una persona mayor de 60 aos], que requiere ir acompaado por un familiar, cuidador o acompaante durante la(s) sesin(es) de EPS individual. Las cuales pueden ser:

          .bg-t.ps-5.p-3.mb-4
            h5(data-aos="fade-down").mb-0 Grupales
          .px-3
            p(data-aos="fade-down") Sujetos (afiliados) que comparten una #[b condicin/situacin de salud] (para quienes se programan las sesiones) grupos de familias segn los hallazgos y la priorizacin de la EAPB, un ejemplo tpico es cuando se presenta violencia intrafamiliar, por lo que se citan inicialmente a los miembros de manera individual, pero tambin se hacen procesos grupales para conocer las dinmicas familiares.

          .bg-t.ps-5.p-3.my-4
            h5(data-aos="fade-down").mb-0 Colectivas
          .px-3
            p(data-aos="fade-down") Corresponden a trabajos realizados por equipos multidisciplinarios con las comunidades en diferentes reas, algunos casos que se pueden mencionar son: visitas a instituciones educativas para dar charlas sobre el consumo de sustancias psicoactivas, el matoneo en la escuela o en las redes sociales, la violencia intrafamiliar, la prostitucin infantil, el embarazo en adolescentes, el uso de armas, los derechos humanos, el origen del conflicto en Colombia, los diferentes temas en salud y el cuidado del medio ambiente, el uso del tiempo libre, entre otros temas familiares.

            ul.lista-ul.mb-0.fa-ul                    
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Habitantes de un barrio/vereda (en conjunto o por momento de curso de vida).
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Organizaciones sociales, comunitarias o comunales.
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Comunidad educativa, estudiantes, padres, docentes, directivos (de preescolar en este caso tambin se puede trabajar con los nios y las nias de ICBF-, primaria, secundaria, universidad, instituciones tcnicas).
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Trabajadores informales (agremiados o no agremiados).
              li 
                i.lista-ul__vineta.c4
                p.mb-0 Familias priorizadas ubicadas en el territorio. 

        .col-lg-10.mt-5
          .row.justify-content-start.mb-4          
            .col-auto.bg3.p-2(data-aos="flip-down")
              h4(data-aos="fade-down").mb-0 Dnde?

          p(data-aos="fade-down") Al realizar el diseo de las sesiones es importante tener en cuenta el lugar donde se van a desarrollar, ya que aqu es vital tener en cuenta diferentes factores que tienen que ver directamente con el paciente o el grupo de pacientes, puesto que el objetivo es que ellos se integren voluntariamente a las actividades, con el fin de que las intervenciones individuales, grupales o colectivas realizadas a corto, mediano o largo plazo tengan impacto y se puedan ver resultados medibles que realmente puedan considerarse vlidos como logros de una estrategia de salud pblica.

          p(data-aos="fade-down").mb-5 Por otro lado, la etapa etaria de cada uno de los pacientes, sus caractersticas, sus condiciones de vida, la situacin geogrfica y an las climticas pueden ser factores que inhiben la participacin activa de los pacientes en este tipo de sesiones; sin embargo, se deben disear las sesiones segn corresponda, teniendo en cuenta las siguientes ubicaciones:
 
      .row.justify-content-center.align-items-start.mb-4 
        .col-lg-5.my-3
          LineaTiempoD.color-primario
            div(numero="1" titulo="Individuales") 
              ul.lista-ul.mb-0.fa-ul                    
                li 
                  i.lista-ul__vineta.c5
                  p.mb-0 IPS.
                li 
                  i.lista-ul__vineta.c5
                  p.mb-0 Hogar del afiliado.  
            div(numero="2" titulo="Grupales") 
              ul.lista-ul.mb-0.fa-ul                    
                li 
                  i.lista-ul__vineta.c6
                  p.mb-0 IPS.
                li 
                  i.lista-ul__vineta.c6
                  p.mb-0 Barrios/veredas (acciones extramurales).
                li 
                  i.lista-ul__vineta.c6
                  p.mb-0 Hogares de los afiliados.
                li 
                  i.lista-ul__vineta.c6
                  p.mb-0 Sitios de trabajo (para el caso de la #[b ARL-Administradora de Riesgos Laborales)].        
            div(numero="3" titulo="Colectivas") 
              ul.lista-ul.mb-0.fa-ul                    
                li 
                  i.lista-ul__vineta.c4
                  p.mb-0 Barrios/veredas.
                li 
                  i.lista-ul__vineta.c4
                  p.mb-0 Espacios educativos (jardines infantiles, colegios, universidades).
                li 
                  i.lista-ul__vineta.c4
                  p.mb-0 Salones comunales.
                li 
                  i.lista-ul__vineta.c4
                  p.mb-0 Espacio pblico (parques naturales, parques barriales, bibliotecas, coliseos, malocas u otros sitios de encuentro comunitario).
        .col-lg-5.my-3
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/104.png', alt='')       

        .col-lg-10.mt-5
          .row.justify-content-start.mb-4          
            .col-auto.bg3.p-2(data-aos="flip-down")
              h4(data-aos="fade-down").mb-0 Cuntas sesiones y duracin de cada una?
          p(data-aos="fade-down") Al planear las sesiones es importante tener en cuenta cuntas se deben realizar y la duracin de cada una de ellas, para esto se tiene un protocolo mdico especfico definido al interior del proceso, el cual inicia con la visita del paciente al mdico general, de familia, crnico o pediatra, segn la etapa etaria en la que se encuentre, quien hace el debido proceso segn corresponda al equipo interdisciplinario.

          p(data-aos="fade-down") Cada profesional recibe el proceso, define el nmero de sesiones y la duracin de cada una de ellas, esto con el fin de hacer seguimiento con el equipo mdico para el caso de las EPS, que llevan los registros mediante las plataformas de telemedicina y que deben entregar informes al Estado para plantear estrategias de salud pblica que garanticen el impacto de las mismas en la consecucin de los derechos de la poblacin colombiana, estas pueden ser: 
      .row.justify-content-center.align-items-start.mb-4
        .col-lg-5.my-3
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/105.png', alt='')        
        .col-lg-5.my-3
          LineaTiempoD.color-primario
            div(numero="1" titulo="Individuales") 
              p De acuerdo con el plan de acompaamiento establecido con/para la persona. Entre #[b 20 a 30 minutos] (deben considerar el establecimiento de #[b formas de seguimiento y el posible acompaamiento virtual, telefnico o por medio de plataformas de telemedicina)]. 
            div(numero="2" titulo="Grupales") 
              p #[b 2 ciclos por momento de curso de vida] (cada ciclo tendr mnimo #[b 3 sesiones y mximo 6]) desarrollados #[b entre uno y tres meses]. En total, una persona debera asistir a mnimo #[b 12 ciclos educativos en el curso de su vida].

